import axios from 'axios';
import { serverUrl } from '../config/config.dev';
// import { serverUrl } from '../config/config.prod';

// import { } from 'dotenv/config';

export function getWorkers() {
    return axios.get(`${serverUrl}/api/workers`);
}
export function getWorkerTrainings(workerId) {
    return axios.get(`${serverUrl}/api/workers/trainings`, {
        params: {
            workerId: workerId
        }
    });
}

export function updateWorkers(workersData) {
    return axios.put(`${serverUrl}/api/workers`, workersData);
}

export function updateWorker(workerData) {
    return axios.put(`${serverUrl}/api/workers`, workerData);
}

export function getTrainings() {
    return axios.get(`${serverUrl}/api/trainings`);
}

export function addTraining(trainingName) {
    return axios.post(`${serverUrl}/api/trainings`, { name: trainingName });
}

export function deleteWorker(workerId) {
    return axios.delete(`${serverUrl}/api/workers`, {
        params: {
            workerId: workerId
        }
    });
}