import React, { useEffect, useState, Fragment } from 'react'

import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';



export default function TrainingDialog(props) {
    const [trainingDialogText, setTrainingDialogText] = useState('');

    return (
        <Dialog open={props.openTrainingDialog} onClose={props.handleCloseTrainingDialog} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">הוספת הדרכה</DialogTitle>
                    <DialogContent>
                        <TextField
                            autoFocus
                            margin="dense"
                            id="name"
                            label="שם הדרכה"
                            type="text"
                            fullWidth
                            onChange={e => props.setTrainingDialogText(e.target.value)}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={props.handleCloseTrainingDialog} color="primary">
                            ביטול
                    </Button>
                        <Button onClick={props.handleSaveTrainingDialog} color="primary">
                            הוספה
                    </Button>
                    </DialogActions>
                </Dialog>
    )
}
