import React from 'react'
import TextField from '@material-ui/core/TextField';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';


import MaterialTable from 'material-table';

export default function CouresesTable(props) {

    const isRequiredOptions = ['נדרש', 'לא נדרש'];
    const statusOptions = ['השתתף', 'לא השתתף'];
    const fields = [
        {
            field: 'name',
            title: 'שם',
            editComponent: propss => (
                <Select
                    value={propss.value || ''}
                    onChange={e => propss.onChange(e.target.value)}
                >
                    {props.trainingsList.map((trainingItem, index) => (
                        <MenuItem key={index} value={trainingItem.name}>{trainingItem.name}</MenuItem>
                    ))}
                </Select>
            )
        },
        {
            field: 'isRequired',
            title: 'נדרש',
            editComponent: propss => (
                <Select
                    value={propss.value || ''}
                    onChange={e => propss.onChange(e.target.value)}
                >
                    {isRequiredOptions.map((option, index) => (
                        <MenuItem key={index} value={option}>{option}</MenuItem>
                    ))}
                </Select>
            )
        },
        {
            field: 'status',
            title: 'סטטוס',
            editComponent: propss => (
                <Select
                    value={propss.value || ''}
                    onChange={e => propss.onChange(e.target.value)}
                >
                    {statusOptions.map((option, index) => (
                        <MenuItem key={index} value={option}>{option}</MenuItem>
                    ))}
                </Select>
            )
        },
        {
            field: 'completionDate',
            title: 'תאריך ביצוע',
            editComponent: props => (
                <TextField
                    type="date"
                    value={props.value || ''}
                    onChange={e => props.onChange(e.target.value)}
                />
            )
        },
        {
            field: 'expiryDate',
            title: 'תאריך תפוגה',
            editComponent: props => (
                <TextField
                    type="date"
                    value={props.value || ''}
                    onChange={e => props.onChange(e.target.value)}
                />
            )
        },
        { field: 'trainerName', title: 'שם מדריך' }
    ]

    return (
        <MaterialTable
            title="הדרכות"
            columns={fields}
            data={props.trainings}
            localization={{
                pagination: {
                    labelDisplayedRows: '{from}-{to} מתוך {count}'
                },
                toolbar: {
                    nRowsSelected: '{0} שורות נבחרו'
                },
                header: {
                    actions: 'פעולות'
                },
                body: {
                    emptyDataSourceMessage: 'אין רשומות להצגה',
                    filterRow: {
                        filterTooltip: 'סינון'
                    },
                    addTooltip: 'הוספה',
                    deleteTooltip: 'מחיקה',
                    editTooltip: 'עריכה',
                    editRow: {
                        deleteText: 'האם אתה בטוח שברצונך למחוק שורה זו?',
                        cancelTooltip: 'ביטול',
                        saveTooltip: 'שמור'
                    }
                },
                pagination: {
                    labelRowsSelect: 'שורות'
                },
                toolbar: {
                    searchTooltip: 'חיפוש',
                    searchPlaceholder: 'חיפוש'
                }
            }}
            editable={{
                onRowAdd: (newData) =>
                    new Promise((resolve) => {
                        setTimeout(() => {
                            resolve();
                            props.onTrainingAdded(newData);
                        }, 600);
                    }),
                onRowUpdate: (newData, oldData) =>
                    new Promise((resolve) => {
                        setTimeout(() => {
                            resolve();
                            if (oldData) {
                                props.onTrainingChange(oldData, newData);
                            }
                        }, 600);
                    }),
                onRowDelete: (oldData) =>
                    new Promise((resolve) => {
                        setTimeout(() => {
                            resolve();
                            console.log(oldData);
                            props.onTrainingDeleted(oldData);
                        }, 600);
                    }),
            }}
        />
    )
}
