import React, { useState, useEffect } from 'react'
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import InputLabel from '@material-ui/core/InputLabel';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import DialogActions from '@material-ui/core/DialogActions';





const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: 'center',
      color: theme.palette.text.secondary,
    },
    workerInfoField: {
        width: 170,
        marginLeft: 20,
        direction: "rtl"
    },
  }));

export default function AddTrainingDialog(props) {
    const classes = useStyles();

    const defaultTraining = {
        name: '',
        isRequired: '',
        status: '',
        completionDate: '',
        expiryDate: '',
        trainerName: ''
    }
    
    const [tempTraining, setTempTraining] = useState({...defaultTraining});


    const isRequiredOptions = ['נדרש', 'לא נדרש'];
    const statusOptions = ['השתתף', 'לא השתתף'];

    const handleFieldChange = (event) => {
        let training = tempTraining;
        training[`${event.target.name}`] = event.target.value;
        setTempTraining(training);
    }

    const handleSave = () => {
        props.handleSaveAddTraining(tempTraining);
        setTempTraining({...defaultTraining})
    }

    const handleCancel = () => {
        props.handleCloseAddTraining();
        setTempTraining({...defaultTraining})
    }

    return (
        <Dialog open={props.openAddTrainingDialog} maxWidth='xl'>
            <DialogContent>
                <Box display="flex" flexDirection="row" flexWrap="wrap" justifyContent="flex-start">
                    <TextField 
                        select 
                        label='שם' 
                        name='name' 
                        className={classes.workerInfoField} 
                        defaultValue={tempTraining.name} 
                        onChange={handleFieldChange}>
                            {props.trainingsList.map((trainingItem, index) => (
                                <MenuItem key={index} value={trainingItem.name}>{trainingItem.name}</MenuItem>
                            ))}
                    </TextField>
                    <TextField 
                        select 
                        className={classes.workerInfoField} 
                        label='נדרש' 
                        name='isRequired' 
                        defaultValue={tempTraining.isRequired} 
                        onChange={handleFieldChange}>
                            {isRequiredOptions.map((option, index) => (
                                <MenuItem key={index} value={option}>{option}</MenuItem>
                            ))}
                    </TextField>
                    <TextField 
                        select 
                        className={classes.workerInfoField} 
                        label='סטטוס' name='status' 
                        defaultValue={tempTraining.status} 
                        onChange={handleFieldChange}>
                            {statusOptions.map((option, index) => (
                                <MenuItem key={index} value={option}>{option}</MenuItem>
                            ))}
                    </TextField>
                    <TextField
                        className={classes.workerInfoField}
                        label='תאריך ביצוע'
                        name='completionDate'
                        InputLabelProps={{
                            shrink: true,
                          }}
                        type="date"
                        defaultValue={tempTraining.completionDate}
                        onChange={handleFieldChange}
                    />
                    <TextField
                        className={classes.workerInfoField}
                        label='תאריך תפוגה' 
                        InputLabelProps={{
                            shrink: true,
                          }}
                        name='expiryDate'
                        type="date"
                        defaultValue={tempTraining.expiryDate}
                        onChange={handleFieldChange}
                    />
                    <TextField
                        className={classes.workerInfoField}
                        label='שם מדריך' 
                        name='trainerName'
                        defaultValue={tempTraining.trainerName}
                        onChange={handleFieldChange}
                    />
                </Box>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleCancel} color="primary">
                    Cancel
                </Button>
                <Button onClick={handleSave} color="primary">
                    Subscribe
                </Button>
            </DialogActions>
        </Dialog>

    )
}
