import React, { useState, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import Box from '@material-ui/core/Box';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import DialogContent from '@material-ui/core/DialogContent';

import CoursesTable from './CouresesTable';

import {
    MuiPickersUtilsProvider,
    KeyboardTimePicker,
    KeyboardDatePicker,
} from '@material-ui/pickers';
import { DialogActions } from '@material-ui/core';

import RTL from '../utils/RTL';

const useStyles = makeStyles({
    dialogTitle: {
        textAlign: "center"
    },
    workerInfoField: {
        width: 170,
        marginLeft: 20,
        direction: "rtl"
    },
    tableContainer: {
        marginTop: '20px'
    }
});

export default function WorkerForm(props) {
    const classes = useStyles();
    const [tempWorker, setTempWorker] = useState({ ...props.worker });

    const handleFieldChange = (event) => {
        let worker = tempWorker;
        worker[`${event.target.id}`] = event.target.value;
        setTempWorker(worker);
    }

    const handleClose = () => {
        props.onClose();
    };

    const handleSave = () => {
        props.onSaveChanges(tempWorker);
    }

    const handleTrainingChange = (oldTrainingData, newTrainingData) => {
        const trainingIndex = tempWorker.trainings.indexOf(oldTrainingData);
        const data = { ...tempWorker };
        data.trainings[trainingIndex] = newTrainingData;
        setTempWorker(data);
    }

    const handleTrainingAdded = (newTrainingData) => {
        const data = { ...tempWorker };
        data.trainings.push(newTrainingData);
        setTempWorker(data);
    }

    const handleTrainingDeleted = (oldTrainingData) => {
        const data = { ...tempWorker };
        data.trainings.splice(data.trainings.indexOf(oldTrainingData), 1);
        setTempWorker(data);
    }



    const fields = [
        { name: 'workerId', label: 'מספר עובד', required: true, type: 'number' },
        { name: 'firstName', label: 'שם פרטי', required: true },
        { name: 'lastName', label: 'שם משפחה', required: true },
        { name: 'birthDate', label: 'תאריך לידה', type: 'datePicker' },
        { name: 'age', label: 'גיל', type: 'number' },
        { name: 'sex', label: 'מין' },
        { name: 'startDate', label: 'תאריך התחלה', type: 'datePicker' },
        { name: 'idNumber', label: 'תעודת זהות' },
        { name: 'phoneNumber', label: 'טלפון' },
        { name: 'phoneNumber2', label: 'טלפון 2' },
        { name: 'city', label: 'עיר' },
        { name: 'address', label: 'כתובת' },
        { name: 'zipCode', label: 'מיקוד' },
        { name: 'departmentHE', label: 'מחלקה' },
        { name: 'departmentEN', label: 'Department' },
        { name: 'roleEmergency', label: 'תפקיד חרום' },
        { name: 'role', label: 'תפקיד' },
        { name: 'team', label: 'תת מחלקה' },
    ]


    return (
        <Dialog onClose={handleClose} open={props.open} maxWidth='xl'>
            <DialogTitle className={classes.dialogTitle}>פרטי עובד</DialogTitle>
            <DialogContent dividers>

                <Box display="flex" flexDirection="row" flexWrap="wrap" justifyContent="flex-start">
                    {fields.map((field, index) => {
                        return field.type === 'datePicker' ?
                            <RTL key={index}>
                                <TextField
                                    id={field.name}
                                    label={field.label}
                                    type="date"
                                    defaultValue={tempWorker[field.name]}
                                    onChange={handleFieldChange}
                                    className={classes.workerInfoField}
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                />
                            </RTL>
                            :
                            <RTL key={index}>
                                <TextField type={field.type} required={field.required} defaultValue={tempWorker[field.name]} className={classes.workerInfoField} id={field.name} label={field.label} onChange={handleFieldChange} ></TextField>
                            </RTL>
                    })
                    }
                </Box>
                <CoursesTable trainingsList={props.trainingsList} trainings={tempWorker.trainings} onTrainingChange={handleTrainingChange} onTrainingAdded={handleTrainingAdded} onTrainingDeleted={handleTrainingDeleted} />
            </DialogContent>
            <DialogActions>
                <Button onClick={handleSave}>
                    שמור
                </Button>
                <Button onClick={handleClose}>
                    בטל
                </Button>
            </DialogActions>
        </Dialog>
    )
}
